/* checkbox style */
/* The checkItems */
.checkItems {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.checkItems input {
  opacity: 1;
  cursor: pointer;
  height: 30px;
  width: 30px;
  background-color: #eee;
  margin-right: 15px;
}

.checkItems label {
  margin-left: 25px;
}

.mediaStyle {
  min-height: 400px;
