@media screen and (max-width: 600px) {
  .optional {
    display: none;
  }
  .mediaStyle {
    min-height: 350px;
  }

  #formMedia {
    width: 90vw;
  }

  #loginCard {
    width: 70%;
    height: 50%;
  }
  #fontStyle {
    text-align: center;
    padding-top: 5px;
    margin-bottom: 5px;
  }

  #mediaFixImg {
    min-height: 120px !important;
  }
}

@media print {
  #introduction {
    /* max-height: none !important; */
    max-height: 30% !important;
    width: 100%;
    margin: auto auto;
  }

  #imgFood {
    max-height: 50vh !important;
  }

  #styleCardInfo {
    margin: 60px 60px 100px 60px;
    display: block;
  }
  .pagebreak {
    clear: both;
    page-break-before: always;
  }
  .notPrint {
    display: none;
  }
}

